import { useState } from 'react';
import { useCanvas } from './useCanvas';
import warzone from './map-overview-bg.jpg';
import './App.css';

function App() {
  const [ menuRef, coordinates, setCoordinates, canvasRef, canvasWidth, canvasHeight ] = useCanvas();
  const [ currentCoord, setCurrentCoord ] = useState({})

  window.addEventListener('contextmenu', function (e) { 
    e.preventDefault(); 
  }, false);

  const handleCanvasClick=(event)=>{
    let _currentCoord  = { x: event.clientX, y: event.clientY }
    // on each click get current mouse location 
    setCurrentCoord(_currentCoord);
    // add the newest mouse location to an array in state 
    setCoordinates([...coordinates, _currentCoord]);
  };

  const handleClearCanvas=(event)=>{
    setCoordinates([]);
  };



  const doAddContract = () =>{
    // on each click get current mouse location 
    const { x, y } = currentCoord;
    // setCurrentCoord({ x: event.clientX, y: event.clientY });

    const _currentCoord = { x, y, contract: true, recon: false };
    // add the newest mouse location to an array in state 
    setCoordinates([...coordinates, _currentCoord]);
  }
  const doAddRecon = () =>{
    const { x, y } = currentCoord;
    const _currentCoord = { x, y, contract: false, recon: true };
    setCoordinates([...coordinates, _currentCoord]);
  }
  const doRemove = () =>{
    const { x, y } = currentCoord;
    coordinates.filter((obj) => obj.x === x && obj.y === y)
  }
 
  return (
    <main id="container" className="App-main" >
      <img  src={warzone} className="img-warzone" alt="logo" />
  
      <canvas 
        className="App-canvas"
        ref={canvasRef}
        width={canvasWidth}
        height={canvasHeight}
        onContextMenu={handleCanvasClick} />

      <div ref={menuRef} id="contextMenu" >
          <div className="contextMenu--contract" onClick={doAddContract} >Start Contract</div>
          <div className="contextMenu--recon" onClick={doAddRecon} >Add Recon</div>
          <div className="contextMenu--remove" onClick={doRemove} >Remove</div>
      </div>
       
      
      <div className="clearButton" >
        <button onClick={handleClearCanvas} > Clear Map </button>
      </div>
    </main>
  );

};

export default App;